.top-left {
  position: absolute;
  top: 10px;
  left: 10px;
}

.bottom-right {
  position: absolute;
  bottom: 10px;
  right: 10px;
}

.season-display {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
.season-display.Summer i {
  color: red;
}
.season-display.Winter i {
  color: blue;
}
.Winter {
  background-color: aliceblue;
}

.Summer {
  background-color: orange;
}
